<script>
import { Vue2InteractDraggable, InteractEventBus } from "vue2-interact";
import { mapState } from "vuex";
import $ from "jquery";
import axios from "axios";

const EVENTS = {
  MATCH: "match",
  SKIP: "skip",
  REJECT: "reject"
};
export default {
  name: "SwipeableCards",
  components: { Vue2InteractDraggable },

  data() {
    return {
      index: this.$store.state.posts.findIndex(
        x => x.id === this.$route.params.id
      ),
      isVisible: true,
      interactEventBus: {
        draggedRight: EVENTS.MATCH,
        draggedLeft: EVENTS.REJECT,
        draggedUp: EVENTS.SKIP
      },
      cards: [],
      bid: 0
    };
  },
  mounted() {
    this.$store.dispatch("loadPosts");
    this.getCards();
    this.initBid();
  },
  computed: {
    current() {
      return this.cards[this.index];
    },
    favorites() {
      return this.$store.state.favorites;
    },
    next() {
      return this.cards[this.index + 1];
    },
    ...mapState(["posts"])
  },
  methods: {
    initBid() {
      this.bid = this.current.price + this.current.raise;
    },
    back() {
      this.index--;
    },
    match() {
      InteractEventBus.$emit(EVENTS.MATCH);
    },
    reject() {
      InteractEventBus.$emit(EVENTS.REJECT);
    },
    skip() {
      InteractEventBus.$emit(EVENTS.SKIP);
    },
    emitAndNext(event) {
      if (event == "match") {
        let seen = false;
        for (let i = 0; i < this.$store.state.favorites.length; i++) {
          if (this.$store.state.favorites[i].n === this.index) {
            seen = true;
          }
        }
        if (!seen) {
          this.$store.dispatch("setFavorite", { n: this.index });
        }
      }

      this.$emit(event, this.index);
      setTimeout(() => (this.isVisible = false), 200);
      setTimeout(() => {
        this.index++;
        this.isVisible = true;
      }, 200);
    },
    getCards() {
      this.cards = this.$store.state.posts;
      this.index = this.$store.state.posts.findIndex(
        x => x.id === this.$route.params.id
      );
    },
    increment() {
      this.bid = parseInt($("#bidinput").val()) + this.current.raise;
    },
    decrement() {
      if (
        parseInt($("#bidinput").val()) - this.current.raise >=
        this.current.price + this.current.raise
      ) {
        this.bid = parseInt($("#bidinput").val()) - this.current.raise;
      } else alert("cant do that");
    },
    async submit() {
      this.bid = parseInt($("bidinput").val());
      if (
        parseInt($("#bidinput").val()) >=
        this.current.price + this.current.raise
      ) {
        alert("all good");
        var newPost = {
          Id: this.current.id,
          Name: this.current.name,
          Image: this.current.image,
          Price: this.bid,
          Raise: parseInt(this.current.raise)
        };
        const url =
          "https://localhost:5001/api/BidItems/" + this.$route.params.id;
        return axios
          .put(url, newPost, {
            headers: {
              "Content-Type": "application/json"
            }
          })
          .then(response => {
            console.log(response);
            this.current.price = this.bid;
            this.initBid();
          })
          .catch(error => {
            console.log(error.response);
          });
      } else {
        alert("your bid is not high enough");
        this.initBid();
      }
    }
  }
};
</script>

